import { API_BASE_URL } from './ApiConfig';

export async function callApi(api, method, request) {
  let options = {
    headers: new Headers({
      'Content-Type': 'application/json',
      Authorization: localStorage.getItem('Authorization'),
    }),
    url: API_BASE_URL + api,
    method: method,
  };

  console.log('request:' + request);
  //POST    
  if (request) {
    console.log('options.body:' + request);
    options.body = JSON.stringify(request);
  }

  //fetch : (       
  //     )
  return fetch(options.url, options)
    .then((res) =>
      res.json().then((json) => {
        console.log('1.(res) : ', res);
        console.log('2.(json) : ', json);
        //   catch      
        if (res.status !== 200) {
          return Promise.reject(res);
        }
        //   
        return json;
      }),
    )
    .catch((err) => {
      console.log('3.(Error) : ', err);
      if (err.status === 403) {
        window.location.href = '/loginForm';
      }
      return Promise.reject(err);
    });

  // await fetch(url, {
  //   method: method,
  //   headers: {
  //     'Content-Type': 'application/json; charset=utf-8',
  //     Authorization: localStorage.getItem('Authorization'),
  //   },
  //   body: JSON.stringify(body),
  // })
  //   .then((res) => {
  //     console.log('(res) : ', res); //response  
  //     console.log('(res) : ', res.status); //response  
  //     console.log('(res) : ', res.ok); //response  
  //     // if (res.ok) {
  //     //   res.json().then((json) => {
  //     //     console.log('(json) : ', json); //response body()
  //     //     return json;
  //     //   });
  //     // } else {
  //     //   throw Error(res.json());
  //     // }
  //     return res.json();
  //   })
  //   .then((json) => {
  //     console.log('(json) : ', json); //response body()
  //     return json;
  //   });
}
// -------------------------------

const LOGIN = 'LOGIN';
const LOGOUT = 'LOGOUT';

export const userLogin = (user) => {
  return {
    type: LOGIN,
    payload: user,
  };
};

export const userLogout = () => {
  return {
    type: LOGOUT,
  };
};

const initstate = {
  isLogin: false,
  user: {
    id: 0,
    userId: '',
    userName: '',
    role: '',
  },
};

const reducer = (state = initstate, action) => {
  switch (action.type) {
    case LOGIN:
      return { isLogin: true, user: action.payload };
    case LOGOUT:
      return {
        isLogin: false,
        user: {
          id: 0,
          userId: '',
          userName: '',
          role: '',
        },
      };
    default:
      return state;
  }
};

export default reducer;
