import React, { useEffect, useState } from 'react';
import { Button, Card, Stack } from 'react-bootstrap';
import { useSelector } from 'react-redux';
import { useParams, useNavigate } from 'react-router-dom'; //react-router-dom6  props.history.push  
import { API_BASE_URL } from '../../js/ApiConfig';
import { callApi, siginin } from '../../js/ApiService';

const BookDetail = () => {
  const { isLogin, user } = useSelector((store) => store); //store   .
  const params = useParams(); // useParams()  .
  const navigate = useNavigate();
  const [book, setBook] = useState({
    id: '',
    title: '',
    author: '',
    quantity: 0,
    borrow: 0,
  });
  const [borrow, setBorrow] = useState({
    userId: user.userId,
    bookId: params.id,
  });

  useEffect(() => {
    callApi('/api/book/' + params.id, 'GET')
      .then((json) => {
        setBook(json);
        // setBorrow({ bookId: json.id, userId: user.userId });
      })
      .catch((error) => {
        console.log('BookDetail Error', error);
      });
  }, []);

  //
  const borrowBook = (e) => {
    e.target.variant = 'secondary'; // ..
    e.target.disabled = true;

    callApi('/api/borrow', 'POST', borrow)
      .then((json) => {
        setBook(json);
      })
      .catch((error) => {
        console.log('BorrowBook Error', error);
      });
  };

  //
  const bookList = () => {
    navigate('/bookList');
  };

  // const deleteBook = () => {
  //   fetch(host + '/book/' + params.id, {
  //     method: 'DELETE',
  //   })
  //     .then((res) => res.text())
  //     .then((res) => {
  //       if (res === 'ok') {
  //         //props.history.push('/'); //   v6   
  //         navigate('/bookList');
  //       } else {
  //         alert('');
  //       }
  //     });
  // };

  // const updateBook = () => {
  //   // props.history.push('/updateForm/' + id);
  //   navigate('/updateForm/' + params.id);
  // };

  return (
    <>
      <h1></h1>
      {/* <Button variant="warning" onClick={updateBook}>
        
      </Button>{' '}
      <Button variant="danger" onClick={deleteBook}>
        
      </Button>{' '}
      <Button variant="secondary" onClick={bookList}>
        
      </Button> */}{' '}
      <hr />
      <Stack gap={2}>
        <Button variant="primary" className="ms-auto" onClick={bookList}>
          
        </Button>
        <div className="d-grid gap-2"></div>
      </Stack>
      <Card>
        <Card.Body>
          <Card.Title>{book.title}</Card.Title>
          <Card.Subtitle className="mb-2 text-muted">
            {book.author}
          </Card.Subtitle>
          <Card.Text className="text-right">
            {book.quantity} / {book.borrow}
          </Card.Text>
          <Button
            variant={book.quantity > 0 ? 'primary' : 'secondary'}
            onClick={borrowBook}
          >
            
          </Button>
        </Card.Body>
      </Card>
    </>
  );
};

export default BookDetail;
