import React, { useState } from 'react';
import { Button, Form } from 'react-bootstrap';
import { useNavigate } from 'react-router-dom';
import { API_BASE_URL } from '../../js/ApiConfig';
import { siginin } from '../../js/ApiService';

const SaveForm = () => {
  const navigate = useNavigate();
  const host = API_BASE_URL + '/api';

  // input     
  const [book, setBook] = useState({
    title: '',
    author: '',
  });

  const changeValue = (e) => {
    setBook({
      ...book,
      [e.target.name]: e.target.value,
    });
  };

  const bookList = () => {
    navigate('/bookList');
  };
  const submitBook = (e) => {
    e.preventDefault(); //submit  action    
    fetch(host + '/book', {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json; charset=utf-8',
      },
      body: JSON.stringify(book), //json  
    })
      .then((res) => {
        console.log(1, res);
        if (res.status === 200) {
          console.log(2, res);
          return res.json();
        } else {
          return null;
        }
      })
      .then((res) => {
        console.log(3, res);
        if (res !== null) {
          console.log(4, res);
          navigate('/bookList'); //props.history.push('/'); //  
        } else {
          alert('  .');
        }
      }); //catch    
  };

  return (
    <Form onSubmit={submitBook}>
      <h1></h1>
      <hr />
      <Form.Group className="mb-3" controlId="formBasicEmail">
        <Form.Label></Form.Label>
        <Form.Control
          type="text"
          placeholder="Enter Title"
          onChange={changeValue}
          name="title"
        />
      </Form.Group>
      <Form.Group className="mb-3" controlId="formBasicEmail">
        <Form.Label></Form.Label>
        <Form.Control
          type="text"
          placeholder="Enter Author"
          onChange={changeValue}
          name="author"
        />
      </Form.Group>
      <Button variant="primary" type="submit">
        
      </Button>{' '}
      <Button variant="secondary" onClick={bookList}>
        
      </Button>
    </Form>
  );
};

export default SaveForm;
