import React, { useEffect, useState } from 'react';
import { Button, Form } from 'react-bootstrap';
import { useNavigate, useParams } from 'react-router-dom';
import { API_BASE_URL } from '../../js/ApiConfig';
import { siginin } from '../../js/ApiService';

const UserUpdateForm = () => {
  const navigate = useNavigate();
  const params = useParams();
  const host = API_BASE_URL + '/api';

  // input     
  const [user, setUser] = useState({
    id: '',
    userId: '',
    userName: '',
    userPwd: '',
    provider: '',
    prividerId: '',
    role: '',
  });

  // api 
  useEffect(() => {
    fetch(host + '/user/' + params.id)
      .then((res) => res.json())
      .then((res) => {
        setUser(res); //  
      });
  }, []);

  const changeValue = (e) => {
    setUser({
      ...user,
      [e.target.name]: e.target.value,
    });
  };

  const submitUser = (e) => {
    e.preventDefault(); //submit  action    
    fetch(host + '/user/' + params.id, {
      method: 'PUT',
      headers: {
        'Content-Type': 'application/json; charset=utf-8',
      },
      body: JSON.stringify(user), //json  
    })
      .then((res) => {
        console.log(1, res);
        if (res.status === 200) {
          return res.json();
        } else {
          return null;
        }
      })
      .then((res) => {
        console.log(2, res);
        if (res !== null) {
          navigate('/user/' + params.id); //v6  props.history  
        } else {
          alert('  .');
        }
      }); //catch    
  };

  const userList = () => {
    navigate('/user/' + user.userId);
  };

  return (
    <Form onSubmit={submitUser}>
      <Form.Group className="mb-3" controlId="userId">
        <Form.Label></Form.Label>
        <Form.Control value={user.userId} disabled />
      </Form.Group>
      <Form.Group className="mb-3" controlId="userPwd">
        <Form.Label></Form.Label>
        <Form.Control
          type="password"
          placeholder="Enter Password"
          onChange={changeValue}
          name="userPwd"
          value={user.userPwd}
        />
      </Form.Group>
      <Form.Group className="mb-3" controlId="userName">
        <Form.Label></Form.Label>
        <Form.Control
          type="text"
          placeholder="Enter User Name"
          onChange={changeValue}
          name="userName"
          value={user.userName}
        />
      </Form.Group>
      <Form.Group className="mb-3" controlId="provider">
        <Form.Label>Provider</Form.Label>
        <Form.Control
          type="text"
          placeholder="Enter Provider"
          onChange={changeValue}
          name="provider"
          value={user.provider !== null && user.provider}
        />
      </Form.Group>
      <Form.Group className="mb-3" controlId="providerId">
        <Form.Label>Provider Id</Form.Label>
        <Form.Control
          type="text"
          placeholder="Enter Provider Id"
          onChange={changeValue}
          name="providerId"
          value={user.providerId !== null && user.providerId}
        />
      </Form.Group>
      <Form.Group className="mb-3" controlId="role">
        <Form.Label></Form.Label>
        <Form.Control
          type="text"
          placeholder="Enter Role"
          onChange={changeValue}
          name="role"
          value={user.role}
        />
      </Form.Group>
      <Button variant="primary" type="submit">
        
      </Button>{' '}
      <Button variant="secondary" onClick={userList}>
        
      </Button>
    </Form>
  );
};

export default UserUpdateForm;
