import React, { useEffect, useState } from 'react';
import { Button } from 'react-bootstrap';
import { useParams, useNavigate } from 'react-router-dom'; //react-router-dom6  props.history.push  
import { callApi } from '../../js/ApiService';

const UserDetail = () => {
  const params = useParams(); // useParams()  .
  const navigate = useNavigate();
  const [user, setUser] = useState({
    id: '',
    userId: '',
    userName: '',
    userPwd: '',
    provider: '',
    prividerId: '',
    role: '',
    update: '',
  });
  console.log('user detail', params);

  useEffect(() => {
    callApi('/api/user/' + params.id, 'GET')
      .then((res) => {
        //    
        setUser(res); //users   
      })
      .catch((err) => {
        console.log('err: ' + err);
      });
  }, []);

  const deleteUser = () => {
    callApi('/api/user' + params.id, 'DELETE')
      .then((res) => {
        if (res.text === 'ok') {
          //props.history.push('/'); //   v6   
          navigate('/userList');
        } else {
          alert('');
        }
      })
      .catch((err) => {
        console.log('err: ' + err);
      });
  };

  const updateUser = () => {
    // props.history.push('/updateForm/' + id);
    navigate('/userUpdateForm/' + params.id);
  };

  const userList = () => {
    navigate('/userList');
  };

  return (
    <div>
      <h1> </h1>
      <hr />
      <Button variant="warning" onClick={updateUser}>
        
      </Button>{' '}
      <Button variant="danger" onClick={deleteUser}>
        
      </Button>{' '}
      <Button variant="secondary" onClick={userList}>
        
      </Button>
      <h3>{user.userId}</h3>
      <h1>{user.userName}</h1>
      <h1>{user.provider}</h1>
      <h1>{user.providerId}</h1>
      <h1>{user.role}</h1>
      <h1>{user.update}</h1>
    </div>
  );
};

export default UserDetail;
