import React, { useEffect, useState } from 'react';
import {
  Button,
  Col,
  Container,
  Form,
  Row,
  Stack,
  Table,
} from 'react-bootstrap';
import { useParams, useNavigate } from 'react-router-dom'; //react-router-dom6  props.history.push  
import BorrowItem from '../../components/BorrowItem';
import { callApi } from '../../js/ApiService';

const MyBorrow = () => {
  const params = useParams(); // useParams()  .
  const navigate = useNavigate();
  const [borrows, setBorrows] = useState([]); //DB  
  const [checkBorrows, setCheckBorrows] = useState(new Set()); //
  const [isAllChecked, setIsAllChecked] = useState(false); //
  const [bChecked, setChecked] = useState(false);

  //stat   
  const checkedBorrowHandler = (borrowId, isChecked) => {
    console.log('checkedBorrowHandler: ' + borrowId + '-' + isChecked);

    if (isChecked) {
      checkBorrows.add(borrowId);
      setCheckBorrows(checkBorrows);
    } else if (!isChecked && checkBorrows.has(borrowId)) {
      checkBorrows.delete(borrowId);
      setCheckBorrows(checkBorrows);
    }

    console.log('checkBorrows: ' + JSON.stringify([...checkBorrows]));
  };

  //  -    ... ??
  const checkHandler = ({ target }) => {
    setChecked(!bChecked);
    allCheckedHandler(target.checked);
  };

  //
  const allCheckedHandler = (isChecked) => {
    if (isChecked) {
      setCheckBorrows(new Set(borrows.map(({ borrowId }) => borrowId)));
      setIsAllChecked(true);
    } else {
      checkBorrows.clear();
      setCheckBorrows(setCheckBorrows);
      setIsAllChecked(false);
    }
  };

  useEffect(() => {
    callApi('/api/borrow/' + params.userId, 'GET')
      .then((res) => {
        //    
        setBorrows(res); //users   
      })
      .catch((err) => {
        console.log('err: ' + err);
      });
  }, []);

  // :    
  const returnBook = () => {
    callApi('/api/borrow/' + params.userId, 'DELETE', [...checkBorrows])
      .then((res) => {
        //    
        alert('  .');
        setBorrows(res);
      })
      .catch((err) => {
        console.log('err: ' + err);
      });
  };

  return (
    <>
      <h1></h1>
      <hr />
      <Stack gap={2}>
        <Button variant="primary" className="ms-auto" onClick={returnBook}>
          
        </Button>
        <div className="d-grid gap-2"></div>
      </Stack>
      <Table striped bordered hover>
        <thead>
          <tr>
            <th>No</th>
            <th></th>
            <th></th>
            <th></th>
            <th></th>
            <th>
              <Form.Check
                checked={bChecked}
                onChange={(e) => checkHandler(e)}
              />
            </th>
          </tr>
        </thead>
        <tbody>
          {borrows.length > 0 &&
            borrows.map((borrow, index) => (
              <BorrowItem
                key={borrow.borrowId}
                borrowbook={borrow}
                isAllChecked={isAllChecked}
                checkedBorrowHandler={checkedBorrowHandler}
                index={index + 1}
              />
            ))}
        </tbody>
      </Table>
    </>
  );
};

export default MyBorrow;
