import React, { useEffect, useState } from 'react';
import { Button, Form } from 'react-bootstrap';
import { useNavigate, useParams } from 'react-router-dom';
import { API_BASE_URL } from '../../js/ApiConfig';
import { siginin } from '../../js/ApiService';

const UpdateForm = () => {
  const navigate = useNavigate();
  const params = useParams();
  const host = API_BASE_URL + '/api';

  // input     
  const [book, setBook] = useState({
    title: '',
    author: '',
  });

  // api 
  useEffect(() => {
    fetch(host + '/book/' + params.id)
      .then((res) => res.json())
      .then((res) => {
        setBook(res); //  
      });
  }, []);

  const changeValue = (e) => {
    setBook({
      ...book,
      [e.target.name]: e.target.value,
    });
  };

  const submitBook = (e) => {
    e.preventDefault(); //submit  action    
    fetch(host + '/book/' + params.id, {
      method: 'PUT',
      headers: {
        'Content-Type': 'application/json; charset=utf-8',
      },
      body: JSON.stringify(book), //json  
    })
      .then((res) => {
        console.log(1, res);
        if (res.status === 200) {
          return res.json();
        } else {
          return null;
        }
      })
      .then((res) => {
        console.log(2, res);
        if (res !== null) {
          navigate('/book/' + params.id); //v6  props.history  
        } else {
          alert('  .');
        }
      }); //catch    
  };

  const cancle = () => {
    navigate('/book/' + params.id);
  };

  return (
    <Form onSubmit={submitBook}>
      <h1></h1>
      <hr />
      <Form.Group className="mb-3" controlId="formBasicEmail">
        <Form.Label>Title</Form.Label>
        <Form.Control
          type="text"
          placeholder="Enter Title"
          onChange={changeValue}
          name="title"
          value={book.title}
        />
      </Form.Group>
      <Form.Group className="mb-3" controlId="formBasicEmail">
        <Form.Label>Author</Form.Label>
        <Form.Control
          type="text"
          placeholder="Enter Author"
          onChange={changeValue}
          name="author"
          value={book.author}
        />
      </Form.Group>
      <Button variant="primary" type="submit">
        
      </Button>{' '}
      <Button variant="secondary" onClick={cancle}>
        
      </Button>
    </Form>
  );
};

export default UpdateForm;
