import React, { useState } from 'react';
import { Button, Form } from 'react-bootstrap';
import { useDispatch } from 'react-redux';
import { useNavigate } from 'react-router-dom';
import { API_BASE_URL } from '../../js/ApiConfig';
import { userLogin } from '../../js/ApiService';

const LoginForm = () => {
  const navigate = useNavigate();
  const dispatch = useDispatch();

  //  
  const [login, setLogin] = useState({
    userId: '',
    userPwd: '',
  });

  //,  
  const changeLogin = (e) => {
    setLogin({
      ...login,
      [e.target.name]: e.target.value,
    });
  };

  // 
  const submitLogin = (e) => {
    e.preventDefault(); //submit  action    

    // ,  .
    fetch(API_BASE_URL + '/login', {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json; charset=utf-8',
      },
      body: JSON.stringify(login), //  , 
    })
      .then((res) => {
        console.log(1, res);

        if (res.status === 200) {
          alert(' !');
          localStorage.setItem(
            'Authorization',
            res.headers.get('Authorization'),
          );
          // localStorage.setItem('accessToken', res.data.data.accessToken)
          // localStorage.setItem('refreshToken', res.data.data.refreshToken)
          // localStorage.setItem('expiredTime', res.data.data.cur_time)
          // axios.defaults.headers.common['x-access-token'] = res.data.data.accessToken
        } else {
          alert(' .');
          // return null;
        }
        return res.json();
      })
      .then((json) => {
        console.log(' ', json);
        dispatch(userLogin(json.data));
        navigate('/'); // 
      })
      .catch((err) => {
        console.log('LoginForm Error:', err);
      });
  };

  //  
  return (
    <Form onSubmit={submitLogin}>
      <Form.Group className="mb-3">
        <Form.Label></Form.Label>
        <Form.Control
          type="text"
          placeholder=" "
          name="userId"
          onChange={changeLogin}
        />
      </Form.Group>

      <Form.Group className="mb-3">
        <Form.Label></Form.Label>
        <Form.Control
          type="password"
          placeholder="Password"
          name="userPwd"
          onChange={changeLogin}
        />
      </Form.Group>
      <Button variant="primary" type="submit">
        
      </Button>
    </Form>
  );
};

export default LoginForm;
