import { useSelector } from 'react-redux';

const Home = () => {
  const { isLogin, user } = useSelector((store) => store);

  console.log('isLogin', isLogin);
  console.log('user', user);

  return (
    <div>
      <h1></h1>
      <hr />
      {isLogin && user.userName + ' .'}
    </div>
  );
};

export default Home;
