import React, { useEffect, useState } from 'react';
import { Button, Col, Container, Form, Row } from 'react-bootstrap';
import { useNavigate, useParams } from 'react-router-dom';
import { callApi } from '../../js/ApiService';

const UpdProfile = () => {
  const navigate = useNavigate();
  const params = useParams();

  // input     
  const [user, setUser] = useState({
    id: '',
    userId: '',
    userName: '',
    userPwd: '',
    provider: '',
    prividerId: '',
    role: '',
  });

  //  
  useEffect(() => {
    //
    callApi('/api/user/' + params.id, 'GET')
      .then((res) => {
        //    

        res.userPwd = '';

        console.log(999, res.userPwd);
        setUser(res); //users   
      })
      .catch((err) => {
        console.log('err: ' + err);
      });
  }, []);

  //  
  const changeValue = (e) => {
    setUser({
      ...user,
      [e.target.name]: e.target.value,
    });
  };

  //
  const submitUser = (e) => {
    e.preventDefault(); //submit  action    

    alert(user.userName);

    callApi('/api/user/' + params.id, 'PUT', user)
      .then((res) => {
        navigate('/myProfile/' + params.id); //v6  props.history  
      })
      .catch((err) => {
        console.log('err: ' + err);
        alert('  .');
      });
  };

  // 
  const myProfile = () => {
    navigate('/myProfile/' + user.id);
  };

  return (
    <>
      <h1> </h1>
      <hr />
      <div class="text-right">
        <Button variant="primary" onClick={submitUser}>
          
        </Button>{' '}
        <Button variant="secondary" onClick={myProfile}>
          
        </Button>
      </div>
      {/* <Container fluid>
        <Row>
          <Col className={'text-right'}>
            <Button variant="primary" onClick={submitUser}>
              
            </Button>{' '}
            <Button variant="secondary" onClick={myProfile}>
              
            </Button>
          </Col>
        </Row>
      </Container> */}
      <Form>
        <Form.Group className="mb-3" controlId="userId">
          <Form.Label></Form.Label>
          <Form.Control value={user.userId} disabled />
        </Form.Group>
        <Form.Group className="mb-3" controlId="userPwd">
          <Form.Label></Form.Label>
          <Form.Control
            type="password"
            placeholder="Enter Password"
            onChange={changeValue}
            name="userPwd"
          />
        </Form.Group>
        <Form.Group className="mb-3" controlId="userName">
          <Form.Label></Form.Label>
          <Form.Control
            type="text"
            placeholder="Enter User Name"
            onChange={changeValue}
            name="userName"
            value={user.userName}
          />
        </Form.Group>
        <Form.Group className="mb-3" controlId="provider">
          <Form.Label>Provider</Form.Label>
          <Form.Control
            type="text"
            placeholder="Enter Provider"
            onChange={changeValue}
            name="provider"
            value={user.provider !== null ? user.provider : ''}
          />
        </Form.Group>
        <Form.Group className="mb-3" controlId="providerId">
          <Form.Label>Provider Id</Form.Label>
          <Form.Control
            type="text"
            placeholder="Enter Provider Id"
            onChange={changeValue}
            name="providerId"
            value={user.providerId !== null ? user.providerId : ''}
          />
        </Form.Group>
        <Form.Group className="mb-3" controlId="role">
          <Form.Label></Form.Label>
          <Form.Select
            name="role"
            onChange={changeValue}
            key={user.role}
            defaultValue={user.role}
          >
            <option value="">-  -</option>
            <option value="ROLE_ADMIN">ADMIN</option>
            <option value="ROLE_MANAGER">MANAGER</option>
            <option value="ROLE_USER">USER</option>
          </Form.Select>
        </Form.Group>
      </Form>
    </>
  );
};

export default UpdProfile;
