import React from 'react';
import { Container, Nav, Navbar } from 'react-bootstrap';
import { useDispatch, useSelector } from 'react-redux';
import { Link, useNavigate } from 'react-router-dom';
import { API_BASE_URL } from '../js/ApiConfig';
import { userLogout } from '../js/ApiService';

const Header = () => {
  const navigate = useNavigate(); //   
  const { isLogin, user } = useSelector((store) => store); //store   .
  const dispatcher = useDispatch(); //store   .
  const logout = () => {
    alert('.');
    dispatcher(userLogout()); //store   .
    localStorage.removeItem('Authorization'); //   .
    navigate('/loginForm'); // ..  ?
  };
  const goManager = () => {
    let myForm = document.myForm;
    window.open('', 'newWin', '');
    myForm.auth.value = localStorage.getItem('Authorization');
    myForm.action = API_BASE_URL + '/index';
    myForm.target = 'newWin';
    myForm.submit();
  };

  return (
    <div>
      <form name="myForm" method="POST">
        <input type="hidden" name="auth"></input>
      </form>
      <Navbar collapseOnSelect expand="lg" bg="dark" variant="dark">
        <Container>
          <Link to="/" className="navbar-brand">
            
          </Link>
          <Navbar.Toggle aria-controls="responsive-navbar-nav" />
          {isLogin === true ? (
            <Navbar.Collapse id="responsive-navbar-nav">
              <Nav className="me-auto">
                <Link to={'/myProfile/' + user.id} className="nav-link">
                  
                </Link>
                <Link to={'/myBorrow/' + user.id} className="nav-link">
                  
                </Link>
                <Link to="/bookList" className="nav-link">
                  
                </Link>
                {user.role === 'ROLE_ADMIN' && (
                  <Link to="#" onClick={goManager} className="nav-link">
                    
                  </Link>
                )}
              </Nav>
              <Nav>
                <Link to="#" onClick={logout} className="nav-link">
                  
                </Link>
              </Nav>
            </Navbar.Collapse>
          ) : (
            <Navbar.Collapse id="responsive-navbar-nav">
              <Nav className="me-auto"></Nav>
              <Nav>
                <Link to="/loginForm" className="nav-link">
                  
                </Link>
              </Nav>
            </Navbar.Collapse>
          )}
        </Container>
      </Navbar>
    </div>
  );
};

export default Header;
