import React, { useEffect, useState } from 'react';
import { Button, Col, Container, Row, Table } from 'react-bootstrap';
import { useParams, useNavigate } from 'react-router-dom'; //react-router-dom6  props.history.push  
import { callApi } from '../../js/ApiService';

const MyProfile = () => {
  const params = useParams(); // useParams()  .
  const navigate = useNavigate();
  const [user, setUser] = useState({
    id: '',
    userId: '',
    userName: '',
    userPwd: '',
    provider: '',
    prividerId: '',
    role: '',
    update: '',
  });
  console.log('user detail', params);

  useEffect(() => {
    callApi('/api/user/' + params.id, 'GET')
      .then((res) => {
        //    
        setUser(res); //users   
      })
      .catch((err) => {
        console.log('err: ' + err);
      });
  }, []);

  const updateUser = () => {
    // props.history.push('/updateForm/' + id);
    navigate('/updProfile/' + params.id);
  };

  return (
    <div>
      <h1> </h1>
      <hr />
      <div class="text-right">
        <Button variant="primary" onClick={updateUser}>
          
        </Button>
      </div>
      <br></br>
      <Container fluid="sm">
        <Row className="bg-light">
          <Col sm={3} md={2} className="p-2">
            
          </Col>
          <Col sm={9} md={10} className="p-2">
            {user.userId}
          </Col>
        </Row>
        <Row>
          <Col sm={3} md={2} className="p-2">
            
          </Col>
          <Col sm={9} md={10} className="p-2">
            {user.userName}
          </Col>
        </Row>
        <Row className="bg-light">
          <Col sm={3} md={2} className="p-2">
            OAuth2
          </Col>
          <Col sm={9} md={10} className="p-2">
            {user.provider}
          </Col>
        </Row>
        <Row>
          <Col sm={3} md={2} className="p-2">
            OAuth2 ID
          </Col>
          <Col sm={9} md={10} className="p-2">
            {user.providerId}
          </Col>
        </Row>
        <Row className="bg-light">
          <Col sm={3} md={2} className="p-2">
            
          </Col>
          <Col sm={9} md={10} className="p-2">
            {user.role}
          </Col>
        </Row>
      </Container>
    </div>
  );
};

export default MyProfile;
