import React, { useEffect, useState } from 'react';
import {
  Accordion,
  Button,
  Col,
  Container,
  Form,
  FormControl,
  InputGroup,
  Row,
  Stack,
} from 'react-bootstrap';
import { useNavigate } from 'react-router-dom';
import BookItem from '../../components/BookItem';
import { callApi } from '../../js/ApiService';

const BookList = () => {
  const [books, setBooks] = useState([]);
  const navigate = useNavigate();
  const [search, setSearch] = useState('');

  //Home     books   .
  //     .
  useEffect(() => {
    callApi('/api/book', 'GET')
      .then((json) => {
        console.log('BookList Ok', json);
        setBooks(json);
      })
      .catch((error) => {
        console.log('BookList Error', error);
      });
  }, []); //    .**

  //
  const searchForm = (e) => {
    e.preventDefault(); //submit  action    

    if (search === '') {
      callApi('/api/book', 'GET')
        .then((json) => {
          console.log('searchForm Ok', json);
          setBooks(json);
        })
        .catch((error) => {
          console.log('searchForm ERROR', error);
        });
    } else {
      callApi('/api/bookSearch', 'POST', search)
        .then((json) => {
          console.log('searchForm Ok', json);
          setBooks(json);
        })
        .catch((error) => {
          console.log('searchForm ERROR', error);
        });
    }
  };

  const searchHandler = (e) => {
    // console.log('searchHandler:', e.target.value);
    setSearch(e.target.value);
  };

  return (
    <>
      <h1></h1>
      <hr />
      <Form onSubmit={searchForm}>
        <Container>
          <div class="row no-gutters">
            <Row className="align-items-center">
              <Col xs={10}>
                <Form.Label htmlFor="inlineFormInput" visuallyHidden>
                  Name
                </Form.Label>
                <Form.Control
                  className="mb-2"
                  id="inlineFormInput"
                  placeholder="   "
                  onChange={searchHandler}
                />
              </Col>
              <Col xs="auto">
                <Button className="mb-2" type="submit">
                  
                </Button>
              </Col>
            </Row>
          </div>
        </Container>
      </Form>
      <Accordion flush>
        {books.length > 0 &&
          books.map((book) => <BookItem key={book.bookId} book={book} />)}
      </Accordion>
    </>
  );
};

export default BookList;
