import { useState } from 'react';
import { Button, Form } from 'react-bootstrap';
import { useNavigate } from 'react-router-dom';
import { API_BASE_URL } from '../../js/ApiConfig';

const JoinForm = () => {
  const host = API_BASE_URL + '/api';
  const navigate = useNavigate();

  const [user, setUser] = useState({
    userId: '',
    userPwd: '',
    userName: '',
    role: '',
  });

  const changeValue = (e) => {
    setUser({
      ...user,
      [e.target.name]: e.target.value,
    });
  };

  const userList = () => {
    navigate('/userList');
  };

  const submitUser = (e) => {
    e.preventDefault();

    fetch(host + '/user', {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json; charset=utf-8',
      },
      body: JSON.stringify(user),
    })
      .then((res) => {
        console.log(res);
        if (res.status === 200) {
          return res.json();
        } else {
          return null;
        }
      })
      .then((res) => {
        if (res !== null) {
          navigate('/userList');
        } else {
          alert('  .');
        }
      });
  };

  return (
    <Form onSubmit={submitUser}>
      <h1></h1>
      <hr />
      <Form.Group className="mb-3" controlId="formBasicId">
        <Form.Label></Form.Label>
        <Form.Control
          type="text"
          placeholder=" "
          onChange={changeValue}
          name="userId"
        />
      </Form.Group>
      <Form.Group className="mb-3" controlId="formBasicPasssword">
        <Form.Label></Form.Label>
        <Form.Control
          type="text"
          placeholder=" "
          onChange={changeValue}
          name="userPwd"
        />
      </Form.Group>
      <Form.Group className="mb-3" controlId="formBasicName">
        <Form.Label></Form.Label>
        <Form.Control
          type="text"
          placeholder=" "
          onChange={changeValue}
          name="userName"
        />
      </Form.Group>
      <Form.Group className="mb-3" controlId="formBasicRole">
        <Form.Label></Form.Label>
        <Form.Select name="role" onChange={changeValue}>
          <option value="USER">USER</option>
          <option value="MANAGER">MANAGER</option>
          <option value="ADMIN">ADMIN</option>
        </Form.Select>
      </Form.Group>
      <Button variant="secondary" onClick={userList}>
        
      </Button>{' '}
      <Button variant="primary" type="submit">
        
      </Button>
    </Form>
  );
};

export default JoinForm;
